* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.ju-dialog {
  font: 14px Segoe UI,Frutiger,Frutiger Linotype,Dejavu Sans,Helvetica Neue,Arial,sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99999999;
  overflow: hidden; }

.ju-dialog-bg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.2;
  -webkit-transition: all .4s;
  transition: all .4s; }

.ju-dialog .ju-dialog-bg {
  background-color: rgba(0, 0, 0, 0.6); }

.ju-dialog-container {
  margin-left: 5%;
  width: 90%; }

@media (min-width: 992px) {
  .ju-dialog-container {
    margin-left: 33.33333333%;
    width: 33.33333333%; } }

@media (min-width: 768px) {
  .ju-dialog-container {
    margin-left: 25%;
    width: 50%; } }

.ju-dialog-warp {
  margin-left: auto;
  margin-right: auto; }

@media (min-width: 992px) {
  .ju-dialog-warp {
    width: 970px; } }

@media (min-width: 768px) {
  .ju-dialog-warp {
    width: 750px; } }

.ju-dialog-box {
  background: white;
  border-radius: 4px;
  position: relative;
  outline: none;
  padding: 15px;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.36, 1.1, 0.2, 1.2);
  transition-property: transform, opacity, box-shadow; }

.ju-dialog-box.shark {
  animation: shark 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0); }

.ju-dialog-box.scale {
  opacity: 0;
  transform: scale(0.5); }

.ju-dialog-box.zoom {
  animation: zoom 0.2s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: scale(0); }

.ju-dialog-close {
  height: 20px;
  width: 20px;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
  opacity: 0.6;
  text-align: center;
  -webkit-transition: opacity 0.1s ease-in;
  transition: opacity 0.1s ease-in;
  font-size: 20px;
  line-height: 14px; }

.ju-dialog-title {
  font-size: 22px;
  line-height: 20px;
  padding-bottom: 15px; }

.ju-dialog-content {
  margin-bottom: 15px;
  height: auto;
  -webkit-transition: height 0.4s ease-in;
  transition: height 0.4s ease-in;
  display: inline-block;
  width: 100%;
  position: relative; }

.ju-dialog-buttons {
  float: right; }

.ju-dialog-clear {
  clear: both; }

.ju-dialog-btn {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  text-shadow: none;
  -webkit-transition: background .1s;
  transition: background .1s;
  color: #333333;
  background-color: white;
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: none;
  border-radius: 4px; }

.ju-dialog-btn:hover {
  background: #dddddd; }

@keyframes shark {
  10%,
  90% {
    transform: translate3d(-2px, 0, 0); }
  20%,
  80% {
    transform: translate3d(4px, 0, 0); }
  30%,
  50%,
  70% {
    transform: translate3d(-8px, 0, 0); }
  40%,
  60% {
    transform: translate3d(8px, 0, 0); } }

@keyframes zoom {
  0% {
    opacity: 0;
    transform: scale(0); }
  100% {
    opacity: 1;
    transform: scale(1); } }
